@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&family=Roboto+Mono&family=Saira:wght@300;600&display=swap);
body{
    min-height: 150vh;
}
*{
  padding: 0;
  margin: 0;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}



@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.Login{
    display: grid;
    grid-template-columns: 2fr 3fr;
    box-shadow: 2px 2px 15px rgb(211, 211, 211), -2px -2px 15px rgb(211, 211, 211);
    min-height: 70vh;
    width: 50%;
    position: absolute;
    left: 25%;
    top: 18%;
    z-index: 3;
    /* transform: translateY(-1000px); */
}
.part{
    padding: 30px 55px 30px 30px;
}
.login{
    padding-bottom: 50px;
    font-size: 30px;
    font-weight: 800;
}
.Login-img{
    font-size: 30px;
    margin: auto 20px 30px 0px;
}
.Login-img img{
    height: 100px;
}
.part1{
    color: white;
    display: flex;
    flex-direction: column;
    background-color: rgb(44, 17, 68);
    background-color: rgb(34, 34, 34);
}
.part2{
    background-color: white;
    padding-top: 50px;
}
.successMsg{
    font-size: 18px;
    padding: 10px;
    margin: 20px 20px 0 20px;
    border-radius: 10px;
    background-color: rgba(0, 144, 0, 0.437);
    color: green;
}
.errorMsg{
    font-size: 18px;
    padding: 10px;
    margin: 20px 20px 0 20px;
    border-radius: 10px;
    background-color: rgba(255, 35, 35, 0.405);
    color: red;
}
.closeLogin{
    padding: 10px;
    right: 0;
    top: 0;
    margin: none;
    position: absolute;
}
.close-login-bar{
    height: 3px;
    width: 40px;
    margin: 2px;
    background-color: black;
    color: white;
}
.close-login-bar1{
    -webkit-transform: rotate(-45deg) translate(-10px,10px);
            transform: rotate(-45deg) translate(-10px,10px);
}
.close-login-bar2{
    -webkit-transform: rotate(45deg) translate(8px,6px);
            transform: rotate(45deg) translate(8px,6px);
}
.part2 input,textarea{
    background-color: rgb(235, 235, 235);
    margin: 15px 20px;
    height: 30px;
    border: none;
    padding: 4px 4px 4px 10px;
}
.part2 textarea{
    height: 60px;
}
.hasAccQuery{
    margin-top: 5px ;
    font-size: 15px;
}
.hasAccQuery span{
    font-size: 15px;
    color: rgb(193, 193, 0);
    color: red;
    font-weight: 800;
    cursor: pointer;
}
.Login-btn{
    background-color:rgb(44, 17, 68);
    background-color: rgb(34, 34, 34);
    padding: 10px 20px;
    color: white;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    text-align: center;
    margin: auto;
    margin-top: 30px;
    cursor: pointer;
}
.google-login{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    text-align: center;
    margin: auto;
    cursor: pointer;
    
}
.onlyForBITSian{
    padding: 30px;
    font-size: 15px;
    color: gray;
}
.material-symbols-outlined {
    font-variation-settings:
    'FILL' 0,
    'wght' 400,
    'GRAD' 0,
    'opsz' 48
}

@media only screen and (max-width: 500px){

.Login{
    grid-template-columns: 1fr;
    left: 0;
    width: 100%;
}
.close-login-bar{
    background-color: white;
}
}
*{
    padding: 0;
    margin: 0;
    font-size: 20px;
    font-family: 'Roboto Mono', monospace;
}
.Navbar{
    display: flex;
    padding: 1em 2em;
    color: white;
    background-color: rgb(41, 39, 39);
    align-items: center;
    height: 10vh;
    flex-wrap: wrap;
}
.nav-ele{
    margin: 0 10px;
    /* flex: 1; */
}
.username{
    margin-left: auto;
    border-left: 5px solid white;
    padding-left: 10px;
}
.username img{
    height: 30px;
    margin-right: 15px;
    cursor: pointer;
}
.username span{
    margin: auto;
}
/* .Cart, */
.Favourites,
.Log-in,
.Log-out,
.history{
    border: 2px solid white;
    padding: 8px 5px;
    cursor: pointer;
    text-align: center;
}
/* .Cart,
.Favourites,
.Log-out{
    display: none;
} */
.Cart{
    border: 1px solid white;
    border-radius: 25%;
    padding: 10px;
}
.Cart svg{
    height: 35px;
    margin: auto;
    margin-right: 20px;
}
.pageicon{
    display: flex;
}
.Navbar .search{
    /* flex: 2; */
    margin: 0 10px;
}
.logo{
    height: 70px;
    object-fit: cover;
}
.head{
    font-size: 30px;
}
.searchin{
    height: 45px;
    width: 100%;
    border: none;
    background-color: rgb(78, 78, 78);
    color: azure;
}
.searchin::-webkit-input-placeholder{
    padding: 5px 10px;
}
.searchin:-ms-input-placeholder{
    padding: 5px 10px;
}
.searchin::placeholder{
    padding: 5px 10px;
}
.searchin:focus{
    border: none;
}
.closeNavbar-for-small-devices{
    display: none;
}

.Profile{
    background-color: rgb(32, 32, 32);
    padding: 2em;
    color: yellow;
    /* transition: 5s; */
}
.Prof-title{
    font-size: 30px;
    padding-bottom: 10px;
}
.profile-inside{
    padding: 1em;
    border-radius: 6px;
    display: flex;
    flex-direction: column;
    background-color: rgb(46, 46, 46);
}
.profile-ele{
    display: flex;
    padding: 10px;
}
.prof-ele-btn{
    cursor: pointer;
    color: white;
    padding: 10px;
    font-weight: 600;
}

@media only screen and (max-width: 600px){
    .Navbar{
        height: auto;
        flex-direction: column;
        overflow-y: hidden;
        margin: auto;
    }
    .Navbar div{
        margin: 20px auto;
    }
    .searchin,.search{
        width: 100%;
    }
    .closeNavbar-for-small-devices{
        display: block;
    }
}
.Carousal{
    display: flex;
    overflow-x: scroll;
}
.Carousal img{
    width: 100%;
}
.Navigator{
    display: flex;
    position: absolute;
    z-index: 2;
    width: 98%;
    margin: 20px;
    height: 55%;
}
.Navigator div{
    background-color: white;
    border-radius: 5px;
    padding: 5px;
    margin-top: auto;
    margin-bottom: auto;
}
.RightArrow{
    margin-left:auto;
}

*{
    z-index: 1;
}

.bar1{
    display: flex;
    margin: 20px;
    justify-content: center;
    flex-wrap: wrap;
}
.bar1 img{
    margin: 20px;
    height: 130px;
    border-radius: 25%;
    cursor: pointer;
}
.disc{
    background-color: gray;
    height: 40px;
    width: 100%;
    border-radius: 20%;
    z-index: -1;
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
    /* transform: skewY(90deg); */
}
.Card{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border-radius: 10px;
    box-shadow: 2px 2px 10px rgb(218, 218, 218);
    transition: 0.125s;
    cursor: pointer;
    margin: 25px 20px;
    padding: 4px 4px;
    width: 253px;
    height: 450px;
    z-index: 1;
}
.Card div{
    margin: 0px auto;
}
.ATF{
    padding: 0px 8px;
    margin: 2px;
    background-color: rgb(191, 195, 198);
    border-radius: 100%;
    color: gray;
    font-size: 25px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}
.red{
    color: red;
    font-size: 25px;
}
.card-img img{
    height: 250px;
    overflow: hidden;
    width: 100%;
}
.number{
    display: flex;
    width: 90%;
    margin: 10px auto;
}
.count{
    margin: 0 15px;
}
.add,.minus{
    box-shadow: 3px 3px 10px rgb(134, 134, 134);
    /* border: 2px solid black; */
    border-radius: 4px;
    padding: 5px 8px;
}
.BN{
    background-color: rgb(80, 216, 80);
    /* border: 4px solid rgb(63, 190, 63); */
    border-radius: 5px;
    color: white;
    width: 95%;
    text-align: center;
    margin: 10px auto;
    padding: 5px;
}
.ATC{
    background-color: orange;
    /* border: 4px solid rgb(226, 148, 4); */
    border-radius: 5px;
    color: white;
    width: 95%;
    margin: 10px auto;
    text-align: center;
    padding: 5px;
}
.buy-opt{
    transition: 0.5s;
    display: none;
    margin: 0;
}
.Card:hover .buy-opt{
    display: inline;
    display: initial;
}
.Card:hover .number{
    display: flex;
}
.Name{
    display: flex;
    align-content: center;
}
.name{
    color: black;
    font-weight: 800;
    font-size: 18px;
    margin-right: 27px !important;
    margin-left: 17px !important;
}
.SD{
    font-size: 15px;
    color: gray;
}
.section{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
h1{
    font-size: 30px;
    margin: 20px;
}
.section4 img{
    height: 200px;
}
*{
    margin: 0;
    padding: 0;
    text-align: center;
}
.sellBlock{
    border: 2px solid black;
    margin: 20px;
    padding: 20px;
    border-radius: 3px;
    display: none;
}
.seller-details-form{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 10px auto;
}
input{
    margin: 10px auto;
    padding: 5px;
}
textarea{
    height: 100px;
}
input,textarea{
    border: 2px solid black;
    border-radius: 3px;
}
.Upload{
    font-size: 25px;
    padding: 5px 8px;
