.Carousal{
    display: flex;
    overflow-x: scroll;
}
.Carousal img{
    width: 100%;
}
.Navigator{
    display: flex;
    position: absolute;
    z-index: 2;
    width: 98%;
    margin: 20px;
    height: 55%;
}
.Navigator div{
    background-color: white;
    border-radius: 5px;
    padding: 5px;
    margin-top: auto;
    margin-bottom: auto;
}
.RightArrow{
    margin-left:auto;
}